/* Switch */
.switch {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 34px;
  cursor: pointer;
}

.switch input {
  /* display: none; */
  visibility: hidden;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 34px;
  transition: background-color 0.4s;
}

.slider::before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  top: 4px;
  left: 4px;
  background-color: white;
  border-radius: 50%;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.slider .icon-left,
.slider .icon-right {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 12px;
  line-height: 1;
  padding: 3px;
  color: #fff;
  transition: opacity 0.4s;
}

.icon-left {
  left: 8px;
}

.icon-right {
  right: 8px;
}

.switch.active .slider {
  background-color: #2196F3;
}

.switch.active .slider:before {
  -webkit-transform: translateX(36px);
          transform: translateX(36px);
}

.switch.active .icon-left {
  opacity: 0;
}

.switch.active .icon-right {
  opacity: 1;
}

