/*
===============
RESET CSS
===============
*/
*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
/*
======X=========
RESET CSS
======X=========
*/
body{
margin: 0;
padding: 0;
box-sizing: border-box;
background-color: #f8f8f8;
}
.appclass{
  width: 50%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (max-width:700px) {
  .appclass{
    width: 100%;
  }
}